.title {
  font-size: 20px;
  font-weight: bold;
}

.about-container {
  padding: 20px;
  overflow: hidden;
  justify-content: center;
}

.about-img {
  height: 300px;
  width: 150px;
  /* object-fit: contain; */
}

.text {
  text-align: justify;
}

@keyframes slide-out {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(-100%);
  }
}

