.Home {
  text-align: center;
  background-color: gray;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile {
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
  align-items: center;
  flex-direction: column;
  gap: 10px;
}

.photo {
  width: 150px;
  height: 150px;
  border-radius: 100px;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.cButton {
  padding: 10px;
  border: solid 1px black;
  cursor: pointer;
  border-radius: 10px;
}

.cText {
  cursor: pointer;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
